/* Section Hero
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

.sectionHero {
 height: 100%;
 text-align: center;
 margin: 0 auto 150px; }

.wrapperBanner {
 margin-top: 60px;
 display: flex;
 flex-direction: column;
 justify-content: center;
 align-items: center; }

.wrapperTextBanner {
 margin-top: 50px; }
.wrapperText {
 margin-bottom: 100px; }


h6 {
 @include font(false,false,false,false,false);
 text-transform: uppercase;
 letter-spacing: 5px;
 font-variant: small-caps; }
