/* Footer
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

.siteFooter {
 background-color: rgba(38,57,89,0.6);
 width: 100%;
 //min-height: 100px
 @include position(fixed,null,null,0px,null); }


.wrapperFooter {
 display: table-cell;
 vertical-align: middle;
 border: 2rem solid transparent;
 //+font(false,false,false,false,60px)
 margin: 0 auto;
 text-align: center;

 .button {
 margin: 1.5rem;
 background-color: rgba(230, 46, 104, 0.7); }
 .button:hover, button:hover {
 border-bottom-color: transparent;
 background-color: rgba(230, 46, 104, 1); } }

.footerStrip {
 width: 100%;
 height: 3.6rem;
 background-color: rgba(38, 57, 89, 0.7);
 a {
 @include font(false, 1.8rem, #FFFFFF, 600, 2);
 background-color: #FF0025;
 text-shadow: 5px 4px 2px rgba(255, 0, 37, 0.5);
 background-clip: text;
 letter-spacing: 1px; } }
